@use "mixin" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 20px !important;
  } //section min 900

  .section-inner {
    .current-output {
      position: relative;
      box-sizing: border-box;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-size: cover;
      background-position: center;
      color: #fff;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //.current-output min 900
      @media screen and (max-width: 900px) {
        padding: 10px;
      } //.current-output min 900

      button.reload{
        z-index: 1;
        position: absolute;
        width: 50px;
        height: 50px;
        right:5%;
        top:15%;
        font-size: 1.5em;
        opacity: 0.7;
        transition: all 0.5s;
        &:active, &:hover{
          opacity: 1;
          transform: rotate(180deg);
        }
      }

      address {
        display: flex;
        overflow: hidden;
        margin-bottom: 10px;
        width: min(90%, 350px);
        padding: 3px 10px;
        box-sizing: border-box;
        background: rgba(#fff, 0.2);
        border-radius: 5px;
        span {
          display: inline-block;
          white-space: nowrap;
          font-weight: 100;
          font-size: 1.2em;
          font-weight: 500;
          animation: marquee 20s linear infinite;
          @keyframes marquee {
            0% {
              transform: translateX(0);
            }
            100% {
              transform: translateX(-100%);
            }
          }
        }
      }
      .circle-container {
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        background: rgba(#000, 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        @include m.square;
        div.sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            width: 100%;
            .sun {
              fill: rgb(255, 196, 0);
              offset-path: path('M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175');
              animation: rotate-sun 10s infinite linear;
              @keyframes rotate-sun{
                0%{transform: rotate(0deg) scale(0.6);}
                100%{transform: rotate(360deg) scale(0.6);}
              }
            } //sun
            .line {
              fill: none;
              stroke: rgba(#FFF, 0.5);
              stroke-width: 3px;
            }//line
            .dash{
              fill: none;
              stroke:rgb(255, 196, 0);
              stroke-width: 6px;
              stroke-dasharray: 1;
            }//dash
          } //svg
          time{
            font-size: 0.85em;
            padding: 3px 10px;
            background: rgb(255, 196, 0);
            border-radius: 10px;
            display: inline-block;
            &.risetime{
              transform: translateX(-30%);
            }
            &.settime{
              float:right;
              transform: translateX(30%);
            }
          }//time
        } //sunset

        .circle-container-inner{
          @include m.posLT(absolute,50%,50%,-50%,-50%);
          width: 90%;
          .temp{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            span{
              position: absolute;
              transform: translate(55px, 10px);
              font-weight: 600;
              i{
                margin-right: 5px;
                font-size: 0.8em;
              }
            }
            em{
              font-size:3em;
              margin-right: -50px;
              position: relative;
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup{font-size: 0.7em;}
            }
            img{
              width: 100px;
              mask-image:url(../assets/img/mask.png);
              mask-size:100%;
              mask-position:center;
              mask-repeat:no-repeat;
            }

          }//temp
          .desc{
            margin-top: -20px;
            text-align: center;
            font-size: 1.3em;
          }//desc
          .etc{
            width: 65%;
            margin:3px auto;
            text-align: center;
            box-sizing: border-box;
            padding: 5px;
            border-radius: 10px;
            background: rgba(#000,0.3);
            span{
              display: inline-block;
              margin: 2px 5px;
              font-size: 0.9em;
                i{
                  margin-right: 3px;
                  font-size: 0.8em;
                  &.fa-location-arrow{
                    margin-right: 7px;
                  }
                }
                b{

                }
                small{
                  font-size: 0.8em;
                }
            }//span
          }//etc
          .time-date{
            margin-top: -2px;
            text-align: center;
            color:#FFF;
            time{
              display: block;
              &.time{
                font-size: 1.2em;
                small{font-size: 0.7em;}
              }
              &.date{
                font-size: 0.9em;
              }
            }
          }
        }//circle-container-inner
      } //circle-container
    } //current-output
  } //section-inner
}
