@use 'mixin' as m;

.comp-map {
  @media screen and (max-width:900px){
    position: fixed;
    z-index: 1; 
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    margin: 0 !important;
    font-size: 1.5em;
    transform: translateX(100%);
    transition: transform .5s;
    &.active{
      transform: translateX(0%);
    }
  }//comp-map max 900 
  .btns{
    display: flex;
    position: absolute;
    top:calc(50% - 25px);
    left: -50px;
    width: 100px;
    height: 50px;
    z-index: 1;
    border-radius: 25px;
    overflow: hidden;
    button{
      width: 50px;
      height: 50px;
      color:#FFF;
      &:first-child{
        background: red;
      }
      &:last-child{
        background: orange;
      }
    }//button
    @media screen and (min-width:900px){
      display: none;
    } //section-inner min 900
  }//btns
  .section-inner {
    @media screen and (max-width:900px){
      height: 100%;  
    } //section-inner max 900
    #map {
      filter: hue-rotate(40deg) brightness(102%) ; 
      height: 100%;
      border-radius: 10px;
    }
  }
}
