@use "mixin" as m;

.comp-hourly {

  .section-inner {
    padding-top: 0 !important;
    overflow: auto;
    h2{
      position: sticky;
      left:0;
    }
    @media screen and (min-width:900px) {
      display: flex;
      flex-direction: column;
    }//section-inner min 900
    .wrap{
      position: relative;
      display: inline-table;
      @media screen and (min-width:900px) {
        height: 100%;
      }//wrap min 900
      @media screen and (max-width:900px) {
        height: 400px;
      }//wrap max 900
      ul{
        display: inline-flex;
        height: 100%;
        li{
          padding-top: 20px;
          width: 70px;
          height: 100%;
          box-sizing: border-box;
          &:nth-child(even){
            background: rgba(#FFF,0.4); 
          }
          p{
            margin: 3px 0;
            font-size: 0.9em;
            img{
              display: inline-block;
              width: 70%;
            }
            text-align: center;
          }
        }//li
      }//ul
      .chart-wrap{
        position: absolute;
        left:5px;
        bottom: 0; 
        width: calc(100% - 35px);
        height: 50%;
      }//chart-wrap
    }//wrap
  } //section-inner
}//comp
 